import React, { Component } from 'react';
import {Container, Box, Typography, TextField, CircularProgress, Button } from "@material-ui/core";
import {firebaseAuth, firestore} from "../firebase";
import Admin from "../media/Admin.png";

class Login extends Component {
    constructor(props){
        super(props);

        this.state = {
            email:"",
            password:"",
            show_progress:false,
        };

        this.handleChange = this.handleChange.bind();
        this.login = this.login.bind();
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value,
        });
    };

    login = ()=>{
         let vaild_data = true;
         this.state.email_error = null;
         this.state.password_error = null;

         if(this.state.email === ""){
             this.state.email_error = "email id required";
             vaild_data=false;
         }
         if(this.state.password === ""){
            this.state.password_error = "password required";
            vaild_data=false;
        }

        if(vaild_data){
            this.state.show_progress = true;
        }
        this.setState({
            update:true,
        })
        if(vaild_data){
            //login
            firestore.collection("USERS")
            .where('email', '==', this.state.email)
            .where('IsAdmin', '==', true)
            .get()
            .then((querySnapshot)=>{
                if(!querySnapshot.empty){
                    //login
                    firebaseAuth.
                    signInWithEmailAndPassword(
                        this.state.email,
                        this.state.password
                    ).then(res=>{
                        this.props.history.replace("/");
                    }).catch(err=>{
                        if(err.code ==='auth/wrong-password'){
                            this.state.password_error="Incorrect Password!"
                        }
                        this.setState({
                            show_progress: false,
                        })
                    })
                }else{
                    this.state.email_error = "Not Allowed!";
                    this.setState({
                        show_progress:false,
                    });
                }
            });
        }
    };
    render() {
        return (
            <Container maxWidth="xs">
              <Box 
              bgcolor="white" 
              boxShadow="2" 
              borderRadius="13px"
              textAlign="center" 
              padding="24px" 
              marginTop="50px">
                <img src={Admin} height="50px" />
                <Typography variant="h5" color="textSecondary">ADMIN</Typography>
                <TextField 
                label="Email"
                id="outlined-size-small"
                variant="outlined"
                fullWidth
                name="email"
                error={this.state.email_error!= null}
                helperText={this.state.email_error}
                onChange={this.handleChange}
                //color="Secondary"
                margin="normal"
                size="small"
              />
              <TextField 
                label="Password"
                id="outlined-size-small"
                type="password"
                name="password"
                error={this.state.password_error!=null}
                helperText={this.state.password_error}
                onChange={this.handleChange}
                variant="outlined"
                fullWidth
                margin="normal"
                size="small"
              />
              <br/><br/>
              {this.state.show_progress ?(
                  <CircularProgress size={25} thickness={6} color="Primary" />
              ) : null}
              <br/>
              <Button 
              disableElevation 
              variant="contained"
              onClick={this.login} 
              color="primary" 
              fullWidth>
                Login
              </Button>
              </Box>
            </Container>
        )
    }
}

export default Login;
