import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import {Typography, Box} from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import {HomeRounded, AccountBoxRounded, PersonRounded, PowerSettingsNewRounded} from '@material-ui/icons';
import AdminFragment from "../Fragments/AdminFragment";
import HomeFragment from "../Fragments/HomeFragment";
import UserFragment from "../Fragments/UserFragment";
import login from "../Pages/login";
import food from "../media/food.png";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerContainer: {
    overflow: 'auto',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

function Dashboard() {
  const classes = useStyles();
  const [fragments, setFragment] = useState("Home")

  const loadFragment=()=>{
      switch (fragments) {
          case "Home":
             return <HomeFragment/>
          case "Admin":
             return <AdminFragment/>
          case "User":
             return <UserFragment/>
          /*case "Logout":
            return <login/>*/
          default:
             break;
      }
  }
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <Typography variant="h6" noWrap>
           <img src={food} height="50px"/>
          </Typography>
          <Typography variant="h5" noWrap>
            <Box marginLeft="10px"> Nutrition</Box>
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <Toolbar />
        <div className={classes.drawerContainer}>
          <List>
              <ListItem button onClick={(e)=>setFragment("Home")}>
                <ListItemIcon >
                    <HomeRounded/>
                </ListItemIcon>
                <ListItemText primary="Home" />
              </ListItem>
              <ListItem button onClick={(e)=>setFragment("Admin")}>
                <ListItemIcon>
                    <AccountBoxRounded/>
                </ListItemIcon>
                <ListItemText primary="Admin" />
              </ListItem>
              <ListItem button onClick={(e)=>setFragment("User")}>
                <ListItemIcon>
                    <PersonRounded/>
                </ListItemIcon>
                <ListItemText primary="User" />
              </ListItem>
          </List>
          <Divider />
          <ListItem button onClick={(e)=>setFragment("Logout")}>
                <ListItemIcon>
                    <PowerSettingsNewRounded/>
                </ListItemIcon>
                <ListItemText primary="Logout" />
              </ListItem>
        </div>
      </Drawer>
      <main className={classes.content}>
        <Toolbar />

       {loadFragment()}
        
        
      </main>
    </div>
  );
}
 export default Dashboard;